export default {
  data: function data() {
    return {
      modalData: {
        title: '添加单选题',
        modalBoolean: false
      },
      nowType: '',
      isDisabled: false,
      FormData: {
        rules: {},
        data: {
          Question: "",
          Item1: "",
          Item2: "",
          Item3: "",
          Item4: "",
          Answer: "",
          KN: "",
          Chapter: "",
          difficulty: ""
        }
      }
    };
  },
  methods: {
    init: function init(type, data) {
      this.nowType = type;

      if (type === 'edit') {
        this.isDisabled = false;
        this.modalData.title = '更新选择题';
        this.FormData.data = data;
      } else if (type === 'add') {
        this.isDisabled = false;
        this.$refs.formList.resetFields();
        this.modalData.title = '添加选择题';
      } else if (type === 'show') {
        this.isDisabled = true;
        this.FormData.data = data;
        this.modalData.title = '查看选择题';
      }

      this.modalData.modalBoolean = true;
    },
    closeModal: function closeModal() {
      this.modalData.modalBoolean = false;
      this.isDisabled = false;
    },
    addNewQS: function addNewQS() {
      this.$emit('callBack', this.FormData.data, this.nowType);
      this.modalData.modalBoolean = false;
      this.isDisabled = false;
    }
  }
};